<template>
  <div id="app">
    <nav class="navbar navbar-custom mb-3">
      <div class="container">
        <a href="#" class="navbar-brand">Github Search</a>
        <vs-input
        id="search"
        type="text"
        v-model="search"
        required
        @keyup="debounceSearch($event)"
        placeholder="Search..."
        style="margin-bottom: 0px;"
        >
          <template #icon>
            <i class='fas fa-search'></i>
          </template>
        </vs-input>
      </div>
    </nav>
    <div class="container">
      <div class="row mt-3" v-if="users.length !== 0">
        <div class="col-md-4">
          <ProfileUser :user="users" />
        </div>
        <div class="col-md-8" v-if="repositories.length !== 0">
          <RepositoriesUser
            v-for="(repo, index) in repositories"
            :key="index + 10"
            :repository="repo"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ProfileUser from "./components/ProfileUser.vue";
import RepositoriesUser from "./components/RepositoriesUser.vue";
import axios from "axios";
import _ from "lodash";

export default {
  name: "app",
  data() {
    return {
      params: {
        url: "https://api.github.com/users",
        client_id: "0bbeff79fa0c5d3f9e01",
        client_secret: "73699f371e3fb06074c17ad064f064d82d29443c",
        per_page: 100,
        sort: "created:asc",
        debounceSearch: null,
        search: ''
      },
      users: [],
      repositories: [],
    };
  },
  created() {
    this.debounceSearch = _.debounce(this.getInfo, 100);
  },
  components: {
    ProfileUser,
    RepositoriesUser,
  },
  methods: {
    getRepositories(user) {
      axios
        .get(
          `${this.params.url}/${user}/repos?per_page=${this.params.per_page}&sort=${this.params.sort}&client_id=${this.params.client_id}&client_secret=${this.params.client_secret}`
        )
        .then(({ data }) => {
          this.repositories = data;
        })
        .catch(() => {
          this.repositories = [];
        });
    },
    getProfile(user) {
      axios
        .get(
          `${this.params.url}/${user}?client_id=${this.params.client_id}&client_secret=${this.params.client_secret}`
        )
        .then(({ data }) => {
          this.users = data;
        })
        .catch(() => {
          this.users = [];
        });
    },
    getInfo($event) {
      const user = $event.target.value;

      this.users = [];
      this.repositories = [];

      if (user.length) {
        this.getProfile(user);
        this.getRepositories(user);
      }
    },
  },
};
</script>

